}
.book-content .book-content__box .box-form textarea {
  padding: 14px;
  background-color: #dbdbdb;
  color: #555;
  display: block;
  width: -moz-fit-content;
  width: fit-content;
  font-size: 1.5rem;
  font-weight: 500;
  outline: none;
  border: none;
  text-align: left;
  grid-column: 2/span 2;
  grid-row: 2;
}
.book-content .book-content__box .box-form__contact-type {
  gap: 1rem;
  width: 120px;
  align-items: center;
