  gap: 0.5rem;
}
.book-content .book-content__box .box-form__contact-type label {
  display: inline-block;
  flex: 0 0 auto;
  margin-right: 1rem;
  text-align: left;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  font-size: 1.5rem;
}
.book-content .book-content__box .box-form__contact-type input {
  padding: 14px;
  width: -moz-fit-content;
  width: fit-content;
  background-color: #dbdbdb;
  color: #555;
  font-size: 1.5rem;
  font-weight: 500;
  outline: none;
